<template>
	<view class="contatle">
		<view class="fanhui">
			<uni-icons type="arrowthinleft" size="30" @click="fanhui"></uni-icons>
		</view>
		<view class="iphonelogn">
			<form @submit="" @reset="" >
				<view class="input">
					<input type="number" value="" placeholder="手机" class="in" v-model="mobile">
				</view>
				<view class="verification">
					<view class="butten" @tap="sendSmsCode('login')">
						验证码
					</view>
					<view class="inputs">
						<input type="number" value=""  class="in">
					</view>
					
				</view>
				<view class="submit">
					确定
				</view>
				<!-- <button form-type="submit">Submit</button>
				<button form-type="reset">Reset</button> -->
			</form>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				mobile: '',
				code: '',
				inviteCode: ''
			};
		},
		methods:{
			fanhui() {
				uni.navigateBack({
					url:'log'
				})
			},
			
			getCode() {
				const randomStr = '00000' + Math.floor(Math.random() * 1000000)
				this.code = randomStr.substring(randomStr.length - 6)
			},
			sendSmsCode(type) {
				if (!/^1\d{10}$/.test(this.mobile)) {
					uni.showModal({
						content: '请输入正确的手机号',
						showCancel: false
					})
					return
				}
				this.getCode()
				uniCloud.callFunction({
					name: 'user-center',
					data: {
						action: 'sendSmsCode',
						params: {
							mobile: this.mobile,
							code: this.code,
							type,
							templateId: '10103'
						}
					},
					success(res) {
						uni.showModal({
							showCancel: false,
							content: JSON.stringify(res.result)
						})
					},
					fail(e) {
						console.error(e)
						uni.showModal({
							showCancel: false,
							content: '发送失败，请稍后再试'
						})
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	.contatle {
		
		.fanhui {
			width: 100%;
			height: 40upx;
			padding-top: 30upx;
		}
		
		.iphonelogn {
			width: 90%;
			min-height: 400upx;
			margin-left: 5%;
			margin-top: 400upx;
			
			.input {
				width: 100%;
				height: 100upx;
				border-bottom-left-radius: 120upx;
				border-bottom-right-radius: 120upx;
				border-top-left-radius: 120upx;
				border-top-right-radius: 120upx;
				background-color: #ededed;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-top: 30upx;
				
				.in {
					height:100%;
					font-size: 40upx;
					// width: 100upx;
				}
			}
			
			.verification {
				width: 100%;
				height: 100upx;
				border-bottom-left-radius: 120upx;
				border-bottom-right-radius: 120upx;
				border-top-left-radius: 120upx;
				border-top-right-radius: 120upx;
				background-color: #ededed;
				display: flex;
				flex-direction: row-reverse;
				// align-items: center;
				// justify-content: center;
				margin-top: 30upx;
				
				.inputs {
					width: 60%;
					height: 100upx;
					
					.in {
						height:100%;
						font-size: 40upx;
						margin-left: 120upx;
					}
				}
				
				.butten {
					width: 40%;
					height: 100%;
					background-color: #333333;
					border-top-right-radius: 120upx;
					border-bottom-right-radius: 120upx;
					display: flex;
					align-items: center;
					justify-content: center;
					color: #818181;
				}
			}
			
			.submit {
				width: 90%;
				height: 200upx;
				margin-left: 5%;
				border-bottom-left-radius: 120upx;
				border-bottom-right-radius: 120upx;
				border-top-left-radius: 120upx;
				border-top-right-radius: 120upx;
				background-color: #f7f7f7;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-top: 50upx;
				box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.3 );
			}
		}
	}
</style>
